import React, { useEffect, useState } from "react";
import { Button, Modal } from "react-bootstrap";
import BootstrapTable from "react-bootstrap-table-next";
import {
  changeDoctorAppointment,
  getAllAppointment,
} from "../../../app/features/appointment/appointment.slice";
import { useDispatch, useSelector } from "react-redux";
import { ClipLoader } from "react-spinners";
import useDebouncedSearch from "../../../hook/useDebouncedSearch";
import { getUserList } from "../../../app/features/userManagement/user.slice";
import SearchInput from "../../../components/SearchInput";


const columns = [
  {
    dataField: "doctorList",
    headerAttrs: {
      hidden: true,
    },
    formatter: (text, row) => (
      <div className="d-flex align-items-center">
        <img
          src={
            row?.imageUrl
              ? row?.imageUrl
              : `https://ui-avatars.com/api/?name=${`${row?.name}`}&background=000071&color=fff`
          }
          alt="doctor"
          className="me-2 dt-round-img"
        />
        <span style={{ lineHeight: "1.2" }}>
          <p className="m-0 table-bold-text">{row?.name}</p>
          <p className="m-0 table-normal-text">{row?.email}</p>
          <p className="m-0 table-normal-text" style={{ color: "#999999" }}>
            {row?.phoneNumber}
          </p>
        </span>
      </div>
    ),
  },
];

export default function SearchDoctorsModal({
  show,
  onClose,
  pageNumber,
  pageSize,
  doctorData,
}) {
  const [doctorValue, setDoctorValue] = useState(null);
  const [doctorList, setDoctorList] = useState([]);

  const [searchValue, handleSearch] = useDebouncedSearch(500);
  const { getAllUser, isLoading } = useSelector((state) => state?.userRole);
  const dispatch = useDispatch();

  const selectRow = {
    mode: "radio",
    clickToSelect: true,
    onSelect: (row) => {
      setDoctorValue(row);
    },
  };

  useEffect(() => {
    const finalData = {
      roleId: 2,
      search: searchValue || null,
    };
    dispatch(getUserList({ finalData }));
  }, [dispatch, searchValue]);

  useEffect(() => {
    if (doctorData) {
      const filteredData = getAllUser?.data?.filter(
        (value) => value?.userId !== doctorData?.doctorId
      );
      setDoctorList(filteredData);
    }
  }, [getAllUser?.data, doctorData]);

  const moveToNext = () => {
    onClose();
    const finalData = {
      pageNo: pageNumber,
      size: pageSize,
      isPagination: true,
    };
    dispatch(getAllAppointment(finalData));
    setDoctorValue(null);
  };
  const assignDoctor = () => {
    const finalData = {
      appointmentId: doctorData?.appointmentId,
      doctorId: doctorValue?.userId,
    };
    dispatch(changeDoctorAppointment({ finalData, moveToNext }));
  };
  return (
    <Modal show={show} onHide={onClose} size="lg" backdrop="static">
      <Modal.Header closeButton>
        <Modal.Title style={{ fontSize: "19px", fontWeight: "bold" }}>
          Doctors
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <label className="header-user-text mb-2">Search Doctor</label>
        <SearchInput onChange={handleSearch} className="mb-3" />
        <span className="doctors-apt-modal-table">
          <BootstrapTable
            columns={columns}
            data={doctorList || []}
            keyField="userId"
            bordered={false}
            wrapperClasses="table-responsive"
            selectRow={selectRow}
          />
        </span>
      </Modal.Body>
      <Modal.Footer>
        <Button
          variant="primary"
          className={`Admin-Add-btn fw-bold w-100 ${isLoading && "disabled"}`}
          onClick={() => assignDoctor()}
          disabled={!doctorValue}
        >
          {isLoading ? (
            <ClipLoader color="white" size={25} className="d-flex m-auto" />
          ) : (
            "Assign"
          )}
        </Button>
      </Modal.Footer>
    </Modal>
  );
}
