import React, { useEffect, useMemo, useState } from "react";
import { Card, Col, Form, Row } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import SearchDoctorsModal from "./SearchDoctorsModal";
import { getAllAppointment } from "../../../app/features/appointment/appointment.slice";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import DataTable from "../../Table/DataTable";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import useDebouncedSearch from "../../../hook/useDebouncedSearch";
import GenericDatePicker from "../../GenericDatePicker";
import SearchInput from "../../SearchInput";

export default function DoctorsAppointment() {
  const [selectedDate, setSelectedDate] = useState(null);
  const [doctorData, setDoctorData] = useState([]);
  const [pageNumber, setPageNumber] = useState(1);
  const pageSize = useMemo(() => 10, []);
  const [show, setShow] = useState(false);
  const [searchValue, handleSearch] = useDebouncedSearch(500);

  const dispatch = useDispatch();

  const { allAppointment, isLoading, isError, isSuccess } = useSelector(
    (state) => state.appointment
  );

  const handleClose = () => setShow(false);

  useEffect(() => {
    const finalData = {
      pageNo: pageNumber,
      size: pageSize,
      isPagination: true,
      search: searchValue ? searchValue : null,
      appointmentDate: selectedDate
        ? moment(selectedDate).format("YYYY-MM-DD")
        : null,
    };
    dispatch(getAllAppointment(finalData));
  }, [dispatch, searchValue, selectedDate, pageNumber]);

  const columns = [
    {
      dataField: "appointmentId",
      text: "ID",
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "doctorName",
      text: "Doctor Name",
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
      formatter: (text, row) => (
        <div className="d-flex align-items-center">
          <img
            src={
              row?.imageUrl
                ? row?.imageUrl
                : `https://ui-avatars.com/api/?name=${`${row?.doctorName}`}&background=000071&color=fff`
            }
            alt="doctor"
            className="me-2 dt-round-img"
          />
          <span style={{ lineHeight: "1.2" }}>
            <p className="m-0 table-bold-text">{row?.doctorName}</p>
            <p className="m-0 table-normal-text">{row?.email}</p>
            <p className="m-0 table-normal-text" style={{ color: "#999999" }}>
              {row?.phoneNumber}
            </p>
          </span>
        </div>
      ),
    },
    {
      dataField: "appointmentDate",
      text: "Date",
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        return cell ? moment(cell).format("DD/MM/YYYY") : "N/A";
      }, 
    },
    {
      dataField: "appointmentStartTime",
      text: "Time",
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "actions",
      text: "Actions",
      sort: false,
      headerStyle: { backgroundColor: "#F1F1F1" },
      headerAlign: "center",
      formatter: (text, row) => (
        <p
          className="m-0 py-1 action-data"
          onClick={() => {
            setDoctorData(row);
            setShow(true);
          }}
        >
          Unassign
        </p>
      ),
    },
  ];

  const handlePageChange = (page, sizePerPage) => {
    setPageNumber(page);
  };

  return (
    <div className="doctor-patient-paymentdetails">
      <h5>Doctors Appointment</h5>
      <Card className="patient-details-card superadmin-date-wrapper">
        <Row className="pt-3 px-4">
          <Col md={3}>
          <SearchInput onChange={handleSearch} />
          </Col>

          <Col
            md={9}
            className="d-flex flex-wrap align-self-center justify-content-end"
          >
            <GenericDatePicker
              selectedDateState={selectedDate}
              onChange={(date) => setSelectedDate(date)}
            />
          </Col>
        </Row>
        {isLoading ? (
          <FullScreenLoader fullHeight={true} />
        ) : isSuccess ? (
          <span className="doctors-apt-table doctor-datatable">
            <DataTable
              tableColumns={columns}
              tableData={allAppointment?.data || []}
              keyField="appointmentId"
              pageNumber={pageNumber}
              totalRecords={allAppointment?.totalCounts}
              dataPerPage={pageSize}
              handlePageChange={handlePageChange}
            />
          </span>
        ) : isError ? (
          <p className="my-5 text-center text-danger fst-italic">
            Network Error
          </p>
        ) : null}
      </Card>
      <SearchDoctorsModal
        show={show}
        onClose={handleClose}
        pageSize={pageSize}
        pageNumber={pageNumber}
        doctorData={doctorData}
      />
    </div>
  );
}
